.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
    overflow: hidden;
}
.circle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.circle:before {
    content: "";
    width: 20px;
    height: 20px;
    border-radius: 50%;
    opacity: 0.7;
    animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
}
.circle1 {
    background-color: #49b8e5;
}
.circle1:before {
    background-color: #49b8e5;
    animation-delay: 100ms;
}
.circle2 {
    background-color: #1e98d4;
}
.circle2:before {
    background-color: #1e98d4;
    animation-delay: 200ms;
}
.circle3 {
    background-color: #2a92d0;
}
.circle3:before {
    background-color: #2a92d0;
    animation-delay: 300ms;
}
.circle4 {
    background-color: #3a88c8;
}
.circle4:before {
    background-color: #3a88c8;
    animation-delay: 400ms;
}
.circle5 {
    background-color: #507cbe;
}
.circle5:before {
    background-color: #507cbe;
    animation-delay: 500ms;
}
@keyframes scale {
    0% {
        transform: scale(1);
    }
    50%, 75% {
        transform: scale(2.5);
    }
    78%, 100% {
        opacity: 0;
    }
}
